<template>
	<div class="body">
		<div class="cash_flow_table_box">
			<div class="cash_flow_table_title flex_between">
				<div class="flex_start">
					<span class="name">汇总数据</span>
					<!-- <span class="ts">提示：回款总额=回款本金+回款收益</span> -->
				</div>
	
			</div>
			<div class="cash_flow_table table">
				<el-table ref="multipleTable" :data="tableData" border style="width: 100%">
					<el-table-column v-for="(tableHeaderitem,index) of tableHeader " :key="index" :prop="tableHeaderitem.param" :label="tableHeaderitem.text" :width='tableHeaderitem.width'  ></el-table-column>
					<template slot="empty">
						<div class="table-nodata">
							<img src="../../images/table_nodata.png"> 没有对应数据
						</div>
					</template>
				</el-table>
			</div>
		</div>
		<div class="cash_flow_table_box">
			<div class="cash_flow_table_title flex_between">
				<div class="flex_start">
					<span class="name">现金流数据</span>
					<el-select v-model="cashFlowType" placeholder="类型" class=" select_border" style="width:140px;margin-left:30px;" clearable @change="typeChangeGetData()">
						<el-option v-for="item in typeList" :key="item.value" :label="item.label" :value="item.value">
						</el-option>
					</el-select>
					<div class="search_box ">
						<el-input v-model="cashFlowProject" prefix-icon="el-icon-search" placeholder="事件名称" clearable>
						</el-input>
					</div>
					<div class="search_box">
						<div class="btn_box">
							<button @click="toSearch()">搜索</button>
						</div>
					</div>
				</div>
				<div class="flex_start ">
					<!-- <div class="table-group-title-btn btn-blue-border " >
						<span class="iconfont icon-daoru"></span>导出
					</div> -->
					<!-- <div class="table-group-title-btn btn-blue flex_start_right" @click="DropboxShow">
						<span class="iconfont icon-jia"></span>添加新的现金流
						<div class="cash_flow_dropbox " :class="{'cash_flow_dropbo_show':cashFlowDropboxShow}" >
							<div class="cash_flow_dropbox_item1"></div>
							<div class="cash_flow_dropbox_item flex_start" @click="toAdd(1)" >
								<span class="iconfont icon-jiaokuan"></span>缴款
							</div>
							<div class="cash_flow_dropbox_item flex_start" @click="toAdd(2)" >
								<span class="iconfont icon-touzi"></span>投资
							</div>
							<div class="cash_flow_dropbox_item flex_start" @click="toAdd(3)" >
								<span class="iconfont icon-huikuan"></span>回款
							</div>
							<div class="cash_flow_dropbox_item flex_start" @click="toAdd(4)">
								<span class="iconfont icon-fenpei"></span>分配
							</div>
							<div class="cash_flow_dropbox_item flex_start" @click="toAdd(5)" >
								<span class="iconfont icon-zhichu "></span>支出
							</div>
							<div class="cash_flow_dropbox_item flex_start" @click="toAdd(6)">
								<span class="iconfont icon-shouru"></span>收入
							</div>
							<div class="cash_flow_dropbox_item1" ></div>
						</div>
					</div> -->
					<el-dropdown trigger="click" @command="toAdd">
						<span class="el-dropdown-link">
							<span class="iconfont icon-jia"></span>添加新的现金流
						</span>
						<el-dropdown-menu slot="dropdown">
							<el-dropdown-item command='1'><span class="iconfont icon-jiaokuan "></span><span class="dropdown_item_name">缴款</span></el-dropdown-item>
							<el-dropdown-item command="2"><span class="iconfont icon-touzi"></span><span class="dropdown_item_name">投资</span></el-dropdown-item>
							<el-dropdown-item command="3"><span class="iconfont icon-huikuan"></span><span class="dropdown_item_name">回款</span></el-dropdown-item>
							<el-dropdown-item command="40"><span class="iconfont icon-fenpei"></span><span class="dropdown_item_name">分配</span></el-dropdown-item>
							<el-dropdown-item command="6"><span class="iconfont icon-shouru"></span><span class="dropdown_item_name">收入</span></el-dropdown-item>
							<el-dropdown-item command="5"><span class="iconfont icon-zhichu "></span><span class="dropdown_item_name">支出-其他</span></el-dropdown-item>
						</el-dropdown-menu>
					</el-dropdown>
				</div>
			</div>
			
			<div class="cash_flow_table table">
				<el-table ref="multipleTable" :data="tableData1" border style="width: 100%" >
					<el-table-column prop="index" label="序号" width="70">
					</el-table-column>
					<el-table-column sortable prop="date" label="日期" width="120">
					</el-table-column>
					<el-table-column sortable prop="type_name" label="类型" width="120">
					</el-table-column>
					<el-table-column sortable prop="event" label="事件" width="230">
					</el-table-column>
					<el-table-column sortable prop="fee" label="金额" width="160">
					</el-table-column>
					<el-table-column sortable prop="should_fee" label="应计金额" width="160">
					</el-table-column>
					<el-table-column sortable prop="note" label="备注" width="150">
					</el-table-column>
					<el-table-column fixed="right" label="操作" width="120">
						<template slot-scope="scope">
							<el-button @click="toCashFlowPage(scope.row)" type="text" size="small">查看</el-button>
							<el-button @click="toDeleteCashFlow(scope.row)" type="text" size="small" v-if="scope.row.is_delete==1">删除</el-button>
						</template>
					</el-table-column>
					<template slot="empty">
						<div class="table-nodata">
							<img src="../../images/table_nodata.png"> 没有对应数据
						</div>
					</template>
				</el-table>
				<el-pagination v-if="total>0" @size-change="everyPageSizeChange" prev-text="上一页" next-text="下一页" background @current-change="changeCurrentPage" :current-page="page" :page-size="pageSize" layout="sizes, prev, pager, next, jumper" :total="total">
    			</el-pagination>
			</div>
		</div>
	</div>
</template>

<script>
	import {dialogConfirm,dialogConfirmHasbtn} from '../../config/mUtils'
	import {} from '../../config/api'
	import {motherFundFof,FofEvents,selfFundCashTotal,selfFundCashDetail,commonDelete} from '../../config/apiSj'
	export default {
		data() {
			return {
				id: '',//母基金/自管基金ID
				
				//筛选列表
				typeList: [
					{
						value: 1,
						label: '缴款'
					},
					{
						value: 2,
						label: '投资'
					},
					{
						value: 3,
						label: '回款'
					},
					{
						value: 4,
						label: '分配'
					},
					
					{
						value: 6,
						label: '收入'
					},
					{
						value: 5,
						label: '支出-其他'
					},
					{
						value: 7,
						label: '支出-管理费'
					},
				],//筛选类型下拉列表

				//分页
				total: 0,
				page: 1,
				pageSize: 10,

				//现金流列表
				cashFlowType: '', //现金流类型
				cashFlowProject: '', //现金流类型项目
				cashFlowDropboxShow:false,//是否下拉
				
				//表格头部，数据
				// tableHeader:[
				// 	{
				// 		param:'type',
				// 		text:'币种',
				// 		width:'100',
				// 	},{
				// 		param:'all_subscribe',
				// 		text:'缴款(万元)',
				// 		width:'120',
				// 	},{
				// 		param:'all_capital',
				// 		text:'分配本金(万元)',
				// 		width:'120',
				// 	},{
				// 		param:'all_profit',
				// 		text:'分配收益(万元)',
				// 		width:'120',
				// 	},
				// ],
				tableHeader:[
					{
						param:'unitValue',
						text:'币种',
						width:'100',
					},{
						param:'subscribe',
						text:'缴款(万元)',
						width:'120',
					},{
						param:'invest',
						text:'投资(万元)',
						width:'120',
					},{
						param:'payback',
						text:'回款返本(万元)',
						width:'120',
					},{
						param:'payback_profile',
						text:'回款收益-税后(万元)',
						width:'160',
					},{
						param:'payback_sum',
						text:'回款总额(万元)',
						width:'120',
					},{
						param:'distribution',
						text:'分配本金(万元)',
						width:'120',
					},{
						param:'distribution_profile',
						text:'分配收益-税前(万元)',
						width:'150',
					},{
						param:'income',
						text:'收入(万元)',
						width:'120',
					},{
						param:'output',
						text:'支出(万元)',
						width:'120',
					},{
						param:'rest_fee',
						text:'账户余额(万元)',
						width:'120',
					},
				],
				tableData: [],
				tableData1: [],
				type: 2,
			}
		},
		created() {
			// element.index = (this.page-1)*this.pageSize+index+1;
			this.id = this.$route.query.id;
			this.getListSummaryData();
			this.getListCashFlowData();
		},
		computed: {
	
		},
		mounted() {},
		methods: {
			
			//顶部导航栏下拉框打开关闭
			DropboxShow() {
				this.cashFlowDropboxShow = !this.cashFlowDropboxShow;
			},
			//跳转添加页面
			toAdd(type){
				let pageName = this.selectCashFlowType( parseInt(type) );
				this.$router.push({
					name: pageName,
					// query: {
					// 	id: this.id,
					// 	unit: this.$route.query.unit,
					// 	name: this.$route.query.name,
					// 	add: 1,
					// }
					query: {
						id: this.id,
						is_add_page: 1,
						type: this.type,
					}
				})
			},
			//获取对应类型页面路由
			selectCashFlowType(type){
				let pageName = '';
				switch(type){
					case 1:
						pageName = 'selfAdministerFunRunDataCashFlowJk';
						break;
					case 2:
						pageName = 'selfAdministerFunRunDataCashFlowInvest';
						break;
					case 3:
						pageName = 'selfAdministerFunRunDataCashFlowBackMoney';
						break;
					case 4:
						pageName = 'selfAdministerFunRunDataCashFlowFpEdit';
						break;
					case 40:
						pageName = 'selfAdministerFunRunDataCashFlowFp';
						break;
					case 5:
						pageName = 'selfAdministerFunRunDataCashFlowPay';
						break;
					case 6:
						pageName = 'selfAdministerFunRunDataCashFlowIncom';
						break;
					case 7:
						pageName = 'selfAdministerFunRunDataCashFlowPayManage';
						break;
				}
				return pageName;
			},
			
			//跳转详情页面
			toCashFlowPage(row){
				// console.log(row);
				// let pageName = '';
				// if(row.type =='缴款'){
				// 	pageName = this.selectCashFlowType(1);
				// }else{
				// 	pageName = this.selectCashFlowType(4);
				// }
				 
				// this.$router.push({
				// 	name: pageName,
				// 	query:{
				// 		unit: this.$route.query.unit,
				// 		name: this.$route.query.name,
				// 		jkId:row.id,
				// 		id:this.id,
				// 		c_type:row.c_type
				// 	}
				// })
				let pageName = this.selectCashFlowType(row.type);
				this.$router.push({
					name: pageName,
					query: {
						id: this.id,
						type: this.type,
						count:row.count,
						is_delete:row.is_delete,
					}
				})
			},

			// 分页功能模块
			everyPageSizeChange(e){
				this.pageSize = e;
				this.getListCashFlowData()
			},
			changeCurrentPage(e){
				this.page = e;
				this.getListCashFlowData()
			},
			//搜索条件修改获取数据
			typeChangeGetData(){
				this.cashFlowProject = '';
				this.page = 1;
				this.getListCashFlowData();
			},
			//去搜索
			toSearch(){
				this.page = 1;
				this.getListCashFlowData();
			},


			//获取现金流汇总数据
			getListSummaryData(){
				selfFundCashTotal({
					petfund_id:this.id,
					// difference_sign:this.$route.query.type,
				}).then(res => {
					if (res.data.msgNo == 1) {
						let data = res.data.payload.data;
						data.forEach(element => {
							if(element.unit==1){
								element.unitValue = '人民币';
							}else{
								element.unitValue = '美元';
							}
						});
						this.tableData = data;
					}
				})
			},
			//获取现金流数据
			getListCashFlowData(){
				selfFundCashDetail({
					page: this.page,
					page_size:this.pageSize,

					type:this.cashFlowType,
					search_content:this.cashFlowProject,
					petfund_id: this.id,
					// difference_sign:this.$route.query.type,
				}).then(res => {
					if (res.data.msgNo == 1) {
						let data = res.data.payload;
						if(data.data.length>0){
							data.data.forEach((element,index) => {
								element.index = (this.page-1)*this.pageSize+index+1;
								if(element.unit==1){
									element.fee = element.fee ? element.fee + '万人民币' : '-' ;
									element.should_fee = element.should_fee ? element.should_fee + '万人民币' : '-';
								}else{
									element.fee = element.fee ? element.fee + '万美元' : '-' ;
									element.should_fee = element.should_fee ? element.should_fee + '万美元' : '-';
								}
							});
						}
						this.total = data.count;
						this.tableData1 = data.data;
					}
				})
			},

			//删除现金流数据
			toDeleteCashFlow(row){
				dialogConfirmHasbtn('确定要删除该现金流数据吗?').then(() => {
					// console.log(row);
					// return false;
					commonDelete({
						difference_sign:this.type,
						count:row.count,
						type:row.type,
					}).then(res => {
						if (res.data.msgNo == 1) {
							dialogConfirm("success", '删除成功');
							let Listlenght = this.tableData1.length;
							if(Listlenght>1){
								this.getListSummaryData();
								this.getListCashFlowData();
							}else{
								if(this.page>1){
									this.page = this.page - 1;
									this.getListSummaryData();
									this.getListCashFlowData();
								}else{
									this.getListSummaryData();
									this.getListCashFlowData();
								}
							}
						}else{
							dialogConfirm('info',res.data.message);
						}
					})
				}).catch(() => {});
			},

			
		}
	}
</script>

<style lang="scss" scoped>
	.body {
		padding: 0 0px 30px 0px;
	}
	
	.cash_flow_table_title {
		font-size: 14px;
		color: #6292F4;
		margin: 22px 0px;
		.ts {
			color: #FF3B30;
			margin-left: 23px;
		}
		.name {
			color: #6292F4;
		}
	}
	
	.circle_blue {
		width: 7px;
		height: 7px;
		border-radius: 50%;
		margin-right: 5px;
		margin-left: 8px;
		background-color: #6292F4;
	}

	.flex_start_right{
		position: relative;
	}
	.cash_flow_dropbox {
		position: absolute;
		border: 1px solid #E3EAEF;
		visibility: hidden;
		transition: all 0.3s;
		top: 30px;
		left: 50%;
		transform: translateX(calc(-50% + 7px));
		opacity: 0;
		box-shadow: 0px 0px 4px 2px rgba(128, 128, 128, 0.1);
		border-radius: 10px;
		z-index: 100;
	}

	.cash_flow_dropbo_show {
		visibility: visible;
		opacity: 1;
		top: 40px;
		left: 50%;
  		transform: translateX(calc(-50% + 7px));
	}
	.cash_flow_dropbox_item {
		width: 180px;
		height: 36px;
		font-size: 14px;
		background: #fff;
		color: #545454;
		position: relative;
		padding-left: 30px;
		&:hover {
			background: #F3F6FB;
			cursor: pointer;
		}
		.iconfont{
			font-size: 16px;
			margin-right: 15px;
		}
		.icon-jiaokuan{
			color: #72D39D;
		}
		.icon-touzi{
			color: #ED7B5C;
		}
		.icon-huikuan{
			color: #5AC8FA;
		}
		.icon-fenpei{
			color: #6292F4;
		}
		.icon-shouru{
			color: #5AC8FA;
		}
		.icon-zhichu{
			color: #6292F4;
		}
	}
	.cash_flow_dropbox_item1{
		background-color: #fff;
		height: 15px;
		width: 180px;
	}


	.search_box{
		width: 150px;
		height: 30px;
		margin-left: 13px;
	}
	
	.btn_box button{
		width: 80px;
		height: 30px;
		color: white;
		font-size: 14px;
		border-radius: 20px;
		background: #6292F4;
	}
</style>


